import React from "react";
import {graphql} from "gatsby";
import {BasePage} from "../components/basePage";

export default ({data}) => (
    <BasePage>
        <div>Hello world!</div>
        <p>{data.site.siteMetadata.title}</p>
    </BasePage>
);

export const query = graphql`
    query {
        site {
            siteMetadata {
                title
            }
        }
    }
`;
